import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface SpotifyState {
  addAssetModalIsShow: boolean;
  assetImportCenterModalIsShow: boolean;
}

const initialState: SpotifyState = {
  addAssetModalIsShow: false,
  assetImportCenterModalIsShow: false,
};

const playlistsSlice = createSlice({
  name: "spotify",
  initialState,
  reducers: {
    showAddAssetModal: (state, action: PayloadAction<boolean>) => {
      state.addAssetModalIsShow = action.payload;
    },
  },
});

export const { showAddAssetModal } = playlistsSlice.actions;
export const playlistsReducer = playlistsSlice.reducer;